body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  height: 100vh;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

/*
 * credit tohttps://codepen.io/osorina/pen/PQdMOO
 */

* {
  margin: 0; 
  padding: 0;
  box-sizing: border-box !important;
}

.monospace {
  color: #CFCFCF;
  font-family: "Roboto Mono", monospace, sans-serif;
  font-size: 18px;
  font-style: normal;
  font-weight: 400;
  line-height: 100%; /* 18px */
  letter-spacing: -0.45px;
  position: absolute;
  left: 7vw;
  /* margin-top: calc(25vh + 310px); */
}

.btnContainer {
  margin-top: 85vh;
  position: absolute;
  width: 100%;
}

.name {
  margin-top: 25vh;
  margin-left: 5vw;
  position: absolute;
  font-family: "Lalezar", sans-serif;
  font-size: 196px;
  font-style: normal;
  font-weight: 400;
  line-height: 80%;

  background: linear-gradient(100deg, #f1f1f1 10%, #8b8b8b 100%);
  background-clip: text;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}

.shadow {
  margin-top: 25vh;
  margin-left: 5vw;
  position: absolute;
  font-family: "Lalezar", sans-serif;
  font-size: 196px;
  font-style: normal;
  font-weight: 400;
  line-height: 80%;
  text-shadow: 12px 8px 4px rgb(0, 0, 0);
}

html, body {
  height: 100%;
}

body {
  /* display: table; */
  width: 100%;
  height: 100%;
  /* background-color: #292929; */
  /* background: linear-gradient(180deg, #303030 0%, #0D0D0D 100%); */
  
  color: #000;
  line-height: 1.6;
  position: relative;
  font-family: "Lalezar", sans-serif;
  overflow-x: hidden;
}

.panel1 {
  width: 100%;
  height: 100vh;
  /* background: radial-gradient(119.08% 83.09% at 0% 33.64%, #4A4A4A 0%, #0D0D0D 100%); */
  /* box-shadow: -5px 0px 9px 9px #0D0D0D; */
  background: radial-gradient(66.5% 66.5% at 0% 29.05%, #4F4F4F 0%, #202020 100%);
  position: relative;
}

.panel2 {
  width: 100%;
  height: -webkit-min-content;
  height: min-content;
  /* background: radial-gradient(75.98% 75.98% at 50% 24.02%, #3F89D3 0%, #1976D2 100%); */
  background: #202020;
  overflow: hidden;
}


.iconContainer {
  /* background: #223A52;
  box-shadow: 3px 4px 5px 2px rgba(0, 0, 0, 0.25); */
  width: 100vw;
  height: 10vw;
  margin-left: auto;
  margin-right: auto;
  margin-top: 10vh;
  text-align: center;
}

.iconContainer img {
  height: 80%;
  display: inline-block;
  padding: 0 2vw 0 2vw;
}

.textContainer {
  background: #223A52;
  box-shadow: 3px 4px 5px 2px rgba(0, 0, 0, 0.25);
  width: 60vw;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
  margin-left: auto;
  margin-right: auto;
  margin-top: 10vh;
  margin-bottom: 10vh;
  padding: 20px;

  /* text */
  white-space: pre-wrap;
  color: #CFCFCF;
  font-family: "Lalazer", sans-serif;
  font-size: 18px;
  font-style: normal;
  font-weight: 600;
  letter-spacing: -0.3px;
  text-align: justify;
}


/* Hide scrollbar for Chrome, Safari and Opera */
body::-webkit-scrollbar {
  display: none;
}

/* Hide scrollbar for IE, Edge and Firefox */
body {
-ms-overflow-style: none;  /* IE and Edge */
scrollbar-width: none;  /* Firefox */
}
.navlink a {
    color: #CFCFCF;
    text-align: center;
    font-family: "Lalazer", sans-serif;
    font-size: 18px;
    font-style: normal;
    padding: 15px;
    font-weight: 700;
    line-height: 100%; /* 20px */
    letter-spacing: -0.3px;
    text-decoration: none;
    transition: color 0.5s;
    /* text-shadow: -1px -1px 12px #000, 1px -1px 0 #000, -1px 1px 0 #000, 1px 1px 0 #000; */

    &:hover {
        color: #8b8b8b;
    }
}

.navlink {
    cursor: pointer;
    position: -webkit-sticky;
    position: sticky;
    padding: 10px;
    margin: 10px 10px 5px 10px;
    display: inline-block;
}

.navbar {
    width: 100%;
    height: -webkit-min-content;
    height: min-content;
    position: fixed;
    z-index: 99;
    text-align: right;
    background-color: #223A52;
}
